body {
  padding: 0;
  margin: 0;
  font-family: HelveticaNeueCyr, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 14px;
}
.wrapper {
  width: 1136px;
  margin: 0 auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.slide-btn {
  position: relative;
  box-sizing: border-box;
  display: block;
  width: 34px;
  height: 34px;
  margin-left: 20px;
  border: 1px solid #CACCCF;
  background-color: transparent;
  font-size: 0;
  cursor: pointer;
}
.slide-btn:hover {
  background-color: #186490;
}
.slide-btn:active {
  background-color: #0C4D73;
}
.slide-btn:disabled {
  border: 1px solid #E8E8E8;
}
.slide-btn--next::before {
  content: url('../../img/svg/arrow_left.svg');
  transform: rotate(180deg);
  position: absolute;
  top: 9px;
  right: 9px;
}
.slide-btn--prev::before {
  content: url('../../img/svg/arrow_left.svg');
  position: absolute;
  top: 9px;
  right: 9px;
}
.link--blue:hover {
  color: #186490;
}
.link--blue:active {
  color: #0C4D73;
}
.link--active {
  color: #186490;
}
.search-popup {
  position: absolute;
  top: 40px;
  left: -100%;
  display: flex;
  justify-content: center;
  background-color: #15191D;
  width: 100%;
  height: 72px;
}
.navigation__search {
  box-sizing: border-box;
  padding: 10px 30px;
  width: 70%;
  align-self: center;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #52575C;
  font-family: HelveticaNeueCyr, Arial, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.3px;
  color: #E8E8E8;
  background: url(../../img/svg/search.svg) no-repeat 97%;
}
.navigation__search::placeholder {
  font-family: HelveticaNeueCyr, Arial, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.3px;
  color: #E8E8E8;
}
.search-popup__close-btn {
  background-color: transparent;
  border: none;
  position: relative;
  top: 6px;
  margin-left: 65px;
  display: flex;
  align-self: center;
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.search-popup__close-btn::before {
  content: '';
  position: absolute;
  top: 10px;
  left: 0;
  width: 20px;
  height: 1px;
  transform: rotate(45deg);
  background-color: #52575C;
}
.search-popup__close-btn::after {
  content: '';
  position: absolute;
  top: 10px;
  left: 0;
  width: 20px;
  height: 1px;
  transform: rotate(-45deg);
  background-color: #52575C;
}
.search-popup__close-btn:hover::after {
  background-color: #E8E8E8;
}
.search-popup__close-btn:hover::before {
  background-color: #E8E8E8;
}
